import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'
import {fromEvent} from '@github-ui/subscription'
// eslint-disable-next-line no-restricted-imports
import {observe} from '@github/selector-observer'
// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {onFocus} from '@github-ui/onfocus'

async function fetchScriptSelector() {
  const repoId = document.querySelector<HTMLInputElement>(
    '#pre_receive_hook_target_hook_attributes_repository_id',
  )!.value
  const container = document.querySelector<HTMLElement>('.js-file-results')!
  const file = document.querySelector<HTMLInputElement>('#pre_receive_hook_target_hook_attributes_script')!.value.trim()

  const contentsAttribute = container.getAttribute('data-contents-url')!
  const url = new URL(contentsAttribute, window.location.origin)
  const params = new URLSearchParams(url.search.slice(1))
  params.append('repository_id', repoId)
  params.append('file', file)
  url.search = params.toString()

  const html = await fetchSafeDocumentFragment(document, url.toString())
  const fileResults = document.querySelector<HTMLElement>('.js-file-results')!
  fileResults.textContent = ''
  fileResults.append(html)
}

function clearSelectedFile() {
  const button = document.querySelector<HTMLElement>('.js-file-select-menu [data-default-label]')!
  button.textContent = button.getAttribute('data-default-label')!

  const selectedNav = document.querySelector('.js-file-select-menu [role="menuitemradio"][aria-checked="true"]')
  if (selectedNav) {
    selectedNav.setAttribute('aria-checked', 'false')
  }
  document.querySelector<HTMLInputElement>('#pre_receive_hook_target_hook_attributes_script')!.value = ''
  const scriptTextField = document.querySelector('.js-script-text-field')
  if (scriptTextField instanceof HTMLInputElement) {
    scriptTextField.value = ''
  }
}

on(
  'details-menu-selected',
  '.js-select-repository',
  async function (event) {
    // Ensure repository ID gets set as input value
    if (!(event.target instanceof Element)) return
    const el = document.getElementById('pre_receive_hook_target_hook_attributes_repository_id')
    if (!(el instanceof HTMLInputElement)) return
    el.value = ((event as CustomEvent).detail.relatedTarget as HTMLButtonElement).value

    await fetchScriptSelector()
    clearSelectedFile()
  },
  {capture: true},
)

onFocus('.js-script-text-field', function (el) {
  if (!(el instanceof HTMLInputElement)) return
  el.addEventListener(
    'blur',
    function () {
      const file = (el.value || '').trim()
      document.querySelector<HTMLInputElement>('#pre_receive_hook_target_hook_attributes_script')!.value = file
    },
    {once: true},
  )
})

observe('.js-hook-environment-channel', {
  subscribe: el =>
    fromEvent(el, 'socket:message', function (event: Event) {
      const target = event.currentTarget as Element
      const data = (event as CustomEvent).detail.data
      target.textContent = data.message
      target.classList.add('updated')
    }),
})

function setEnforcementValue() {
  const enforcementCheckbox = document.querySelector<HTMLInputElement>('.js-activate-enforcement')!
  const enforcement = document.querySelector<HTMLInputElement>('.js-enforcement-value')!
  let dataAttr = 'data-testing'

  if (enforcementCheckbox.checked) {
    const enable = document.querySelector('.js-toggle-enforcement')
    if (enable instanceof HTMLInputElement) {
      dataAttr = enable.checked ? 'data-enabled' : 'data-disabled'
    }
  }

  enforcement.value = enforcement.getAttribute(dataAttr) || ''
}

function setFinalValue() {
  const notFinal = document.querySelector<HTMLInputElement>('.js-toggle-final')!
  const targetFinal = document.querySelector<HTMLInputElement>('.js-final-value')!
  targetFinal.value = (!notFinal.checked).toString()
}

on('click', '.js-activate-enforcement', function ({currentTarget}) {
  if (!(currentTarget instanceof HTMLInputElement)) return
  const enforcementActivated = currentTarget.checked
  const enforcementLabel = document.querySelector('.js-enforcement-label')
  const testModeWarning = document.querySelector('.js-test-mode-warning')

  if (enforcementLabel == null || testModeWarning == null) {
    return
  }

  enforcementLabel.classList.toggle('disabled', !enforcementActivated)
  /* eslint-disable-next-line github/no-d-none */
  testModeWarning.classList.toggle('d-none', enforcementActivated)

  for (const enforcementControl of document.querySelectorAll<HTMLInputElement>('.js-enforcement-control')) {
    enforcementControl.disabled = !enforcementActivated
    if (!enforcementActivated) enforcementControl.checked = false
  }

  setEnforcementValue()
  setFinalValue()
})

on('click', '.js-toggle-enforcement', setEnforcementValue)
on('click', '.js-toggle-final', setFinalValue)
